import { Container } from 'react-bootstrap';
import './Body.css';
import { useEffect,useState } from 'react';
import ReactMarkdown from 'react-markdown';

export const Progress = () => {
    return (
        <Container className="overflow-auto text-left parent" style={{ width: "50%",height: "80%",marginLeft: "0px" }}>
            <div>
                <header className='d-flex flex-row align-items-end'>
                    <h1 style={{ color: "white" }}></h1>
                    <p style={{ fontSize: "14px",marginBottom: "8px",marginLeft:"5px" }}></p>
                </header>
            <Text style={{color: "white"}} />
            </div>
        </Container>
    );
};

const Text = () => {
    const [markdown, setMarkdown] = useState("");

    useEffect(() => {
        fetch(`${process.env.PUBLIC_URL}/TestProgress.md`)
        .then((m) => {
            return m.text();
        })
        .then((md) => {
            setMarkdown(md);
        });
    }, []);

    return (<ReactMarkdown>{markdown}</ReactMarkdown>)
}